.header {
  background-color: #f9fafa;
  width: 100%;
  padding: 0 15px;
  position: fixed;
  top: 0;
  left: 0;
  border-bottom: 1px solid #eef1f1;
  height: 80px;
  z-index: 99;

  @media (min-width: 1024px) {
    background-color: #fff;
  }
}

.header-content {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
}

.header-content-wrapper {
  display: flex;
  align-items: center;
  gap: 16px;

  @media (min-width: 1024px) {
    gap: 24px;
  }
}

#burger-menu {
  cursor: pointer;
  height: 22px;
  width: 22px;
  position: relative;
  z-index: 99;

  span,
  span:before,
  span:after {
    background: #555f62;
    display: block;
    height: 2px;
    opacity: 1;
    position: absolute;
    transition: 0.3s ease-in-out;
  }

  span:before,
  span:after {
    content: '';
  }

  span:before {
    left: 0px;
    top: -10px;
    width: 10px;
  }

  span {
    right: 0px;
    top: 10px;
    width: 20px;
  }

  span:after {
    left: 0px;
    top: 10px;
    width: 13px;
  }

  &.close {
    span:before {
      top: 0px;
      transform: rotate(90deg);
      width: 22px;
    }

    span {
      transform: rotate(-45deg);
      top: 10px;
      width: 22px;
    }

    span:after {
      top: 0px;
      left: 0;
      transform: rotate(90deg);
      opacity: 0;
      width: 0;
    }
  }
}

@media (min-width: 1024px) {
  #burger-menu {
    display: none;
  }
}

#menu {
  z-index: 999;
  min-width: 100%;
  min-height: 100%;
  position: fixed;
  top: 80px;
  left: 0;
  display: none;

  &.overlay {
    display: block;
  }
}

@media (min-width: 1024px) {
  #menu {
    &.overlay {
      display: none;
    }
  }
}

.mobile-menu-wrapper {
  overflow: auto;
  height: calc(100vh - 80px);
  background-color: #f9fafa;
}

.mobile-menu {
  background-color: #fff;
}

.arrow-icon {
  svg {
    transition: transform 0.3s ease;
  }
}

.dropdown-mobile {
  &__box {
    padding: 32px 15px;
    border-bottom: 1px solid #d1d5d6;
  }

  &__title-parent {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__items {
    display: flex;
    flex-direction: column;
    gap: 16px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s ease;

    &.active {
      max-height: 200px;
      padding: 20px 0 0 0;
    }
  }

  &__title {
    color: #141719;
    font-size: 18px;
    font-weight: bold;
  }

  &__link {
    color: #555f62;
    display: block;
    font-size: 16px;
    text-decoration: none;
    padding-right: 30px;
  }

  &__box {
    &.active {
      .arrow-icon svg {
        transform: rotate(180deg);
      }
    }
  }
}

.dropdown {
  display: none;

  @media (min-width: 1024px) {
    display: flex;
    align-items: center;
    gap: 30px;
    height: 100%;
  }

  &__item {
    color: #222;
    font-size: 16px;
    transition: opacity 0.25s ease-in-out;
    cursor: pointer;
  }

  &__line {
    width: 100%;
    height: 2px;
    transform-style: preserve-3d;
    background-color: #182cc0;
    border-radius: 1px;
    margin-bottom: -1px;
    position: absolute;
    top: auto;
    bottom: 0;
    left: 0;
    right: 0;
    transform: scaleX(0);
    transition: 0.6s;
  }

  &__list {
    min-width: 200px;
    grid-row-gap: 16px;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 32px;
    padding-bottom: 32px;
    display: none;
    pointer-events: none;
    position: absolute;
    top: 80px;
    left: 0;
  }

  &__box {
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 102;

    &.active {
      .dropdown__line {
        transform: scaleX(1);
      }

      .dropdown__list {
        pointer-events: all;
        display: flex;
      }
    }
  }

  &__bg {
    min-height: 248px;
    width: 100%;
    background-color: #fff;
    padding: 32px 15px;
    display: none;
    position: absolute;
    top: 80px;
    left: 0;
  }

  &__bg-active {
    display: flex;
  }

  &__bg-content {
    display: flex;
  }

  &__bg-box {
    display: flex;
    flex-direction: column;
    gap: 22px;
  }

  &__bg-link {
    color: #182cc0;
    transition: color 0.25s ease-in-out;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 700;
    gap: 3px;

    &:hover {
      color: #3295ce;
    }
  }
}

.logo-mobile {
  @media (min-width: 1024px) {
    display: none;
  }
}

.logo-desktop {
  display: none;

  @media (min-width: 1024px) {
    display: block;
  }
}

.header-footer {
  padding: 32px 0 0;
  background-color: #fff;

  @media (min-width: 1024px) {
    padding: 0;
  }
}

.header-footer-title {
  font-size: 16px;
  color: #141719;
  font-weight: bold;
  padding: 0 15px;

  @media (min-width: 1024px) {
    padding: 0;
  }
}

.header-footer-list {
  margin-top: 32px;
  margin-bottom: 48px;
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 0 15px;

  @media (min-width: 1024px) {
    padding: 0;
  }
}

.header-footer-item {
  display: flex;
  align-items: center;
  gap: 8px;

  & > a {
    color: #555f62;
    font-size: 16px;
  }
}

.header-social {
  padding: 0 15px;

  @media (min-width: 1024px) {
    padding: 0;
  }
}

.header-social-parent {
  display: flex;
  align-items: center;
  gap: 15px;
  margin: 24px 0;

  & > a {
    width: 52px;
    height: 52px;
    background-color: #f9fafa;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.header-social-title {
  font-size: 16px;
  color: #141719;
  font-weight: bold;
}

.header-footer-bottom {
  background-color: #f9fafa;
  padding: 24px 15px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: space-between;

  & > a {
    color: #555f62;
    font-size: 12px;
  }
}

.header-footer-box {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 12px;
}
